body {
  margin: 0;
  font-family:Verdana, Geneva, Tahoma, sans-serif;
  align-content: center;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.welcome {
  text-align: left;
  padding: 10px;
  max-width: 1000px;
  align-items: center;
  margin: 0 auto;
}

.navbar {
  font-family:Verdana, Geneva, Tahoma, sans-serif;
  padding: 10px;
  display: flex;
  align-items: center;
  max-width: 700px;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
}

.navbar a {
  margin-left: 16px;
  text-decoration:double;
  padding: 6px;
}

.navbar a:hover {
  color: #05603c;
  box-shadow: 1px 3px 5px rgba(0,0,0,0.1);
}

.resume {
  align-items: center;
  margin: 0 auto;
  text-align: center;
}